import tab from 'base/tab/tab'
import Tarbar from 'base/tarbar/tarbar'
import { getHeader } from 'api/api'
import Scroll from 'base/scroll/scroll'
import BScroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default {
  data () {
    return {
      data: [],
      tops: [],
      currentIndex: 1
    }
  },
  created () {
    this._getHeader()
  },
  mounted () {
  },
  watch: {
  },
  methods: {
    selectCity () {
      this.$router.push({
				path: `/city`
			})
    },
    _getHeader () {
      getHeader().then((res) => {
        console.log(res.data)
        this.data = res.data
      })
    },
    ...mapMutations({
      setGoods: 'SET_GOODS'
	  })
  },
  components: {
    tab,
    Tarbar,
    Scroll
  }
}



