import BScroll from 'better-scroll'
import { getTab } from 'api/api'
export default {
	name: 'tab',
	data () {
		return {
			tabs: [],
			currentIndex: 0
		}
	},
	created () {
		this._getTab()
	},
	mounted () {
		this.$nextTick(() => {
			this._initTab()
		})
  },
	watch: {
		'tabs' () {
			this.$nextTick(() => {
				this._initTab()
			})
		},
	},
	methods: {
		_initTab () {
			// 计算每一个li的宽度
			let tabList = document.querySelectorAll('.tab-list')[0].children
			let tabWidth = 0
			for(let i=0; i<tabList.length; i++) {
				tabWidth += tabList[i].offsetWidth
			}
			let width =  tabWidth
			this.$refs.tabList.style.width = width + 'px'
			this.$nextTick(() => {
				this.tabScroll = new BScroll(this.$refs.tabWrapper, {
					scrollX: true,
					eventPassthrough: 'vertical'
				})
			})
		},
		_getTab () {
			getTab().then((res) => {
				this.tabs = res.data
			})
		},
		selectTabs(index,item) {
			this.currentIndex = index
			this.$emit('select',index,item)
		}
	}
}



